import {TestSuiteConfig} from "./models/TestSuiteConfig";
import {EnvResourceConfig} from "./models/EnvResourceConfig";
import {JsonProperty} from "json-typescript-mapper";

export class BusybeeConfig {
  @JsonProperty({clazz: EnvResourceConfig, name: 'envResources'})
  envResources: EnvResourceConfig[];
  @JsonProperty({clazz: TestSuiteConfig, name: 'testSuites'})
  testSuites: TestSuiteConfig[];
}
